.projectContainer {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 20%;
  scroll-behavior: smooth;
  /*margin-bottom: 50px;*/
  width: 60%;
  margin-top: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  column-gap: 50px;
  /*display: flex;
  flex-wrap: wrap;*/
  overflow-y: auto;
  overflow-x: hidden;
  overflow-wrap: unset;
  scroll-behavior: smooth;
  flex-direction: row;
  border-radius: 10px;
  cursor: all-scroll;
  align-items: flex-start;
  border-radius: 5px;
  max-height: 60vh;
  height: 100vh;
  animation: down 0.5s linear;
}
.projectContainer::-webkit-scrollbar {
  display: none;
}

.header-project {
  position: absolute;
}
.image {
  width: 100%;
  height: 60%;
  border-radius: 8px;
  filter: brightness(0.6) contrast(0.6);
}
.image:hover {
  filter: brightness(100%) contrast(0.9);
}
.imagedisplay {
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  /* margin-top: 50px;*/
  text-align: center;
  border: 1px solid black;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}
.imagedisplay:hover .desc {
  opacity: 1;
  height: 60%;
}
.imagedisplay:hover .heading {
  opacity: 0;
}
.heading {
  align-items: center;

  color: white;
  display: flex;

  font-family: sans-serif;
  justify-content: center;
  left: 20%;
  top: 15%;
  right: 20%;

  position: absolute;

  text-align: center;
  opacity: 1;
  font-size: 20px;
}
.desc {
  color: white;
  display: flex;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  justify-content: center;
  left: 0;
  top: -10px;
  right: 20%;
  border-radius: 8px;
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0.3;
  text-align: left;
  opacity: 0;
  width: 100%;
  height: 0;
  transition: 0.5s ease;
}
.desc > h3 {
  text-align: left;

  font-size: 20px;
}
.desc > p {
  text-align: left;
  font-size: 15px;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
input {
  border-style: none;
  border-bottom: 1px solid black;

  width: 200px;
  height: 30px;
}
.buttons {
  display: flex;
  column-gap: 30px;
}
.buttonClass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  column-gap: 20px;
  margin-bottom: 5%;
  position: absolute;
  /* top: 15vh;*/
  top: 150px;
  left: 3%;
  opacity: 1;
  width: 0;
  animation: arrangeButton 0.6s linear;
}
@keyframes down {
  0% {
    transform: translateZ(200px) transLateY(40px);
  }
  80% {
    transform: translateZ(-10px) transLateY(0px);
  }
  100% {
    transform: translateZ(0px) transLateY(0px);
  }
}
@keyframes up {
  0% {
    transform: translateZ(200px) transLateY(-200px);
  }
  80% {
    transform: translateZ(-10px) transLateY(0px);
  }
  100% {
    transform: translateZ(0px) transLateY(0px);
  }
}

@keyframes arrangeButtons {
  0% {
    transform: translateY(-400px);

    opacity: 0;
  }
  80% {
    transform: translateY(0px);
    opacity: 0.5;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes arrangeButton {
  0% {
    transform: translateY(-400px);

    opacity: 0;
  }

  100% {
    transform: translateY(0px);
    opacity: 0;
  }
}

@keyframes grow {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes movefromleft {
  0% {
    transform: translateZ(200px) transLateX(-40px);
  }
  80% {
    transform: translateZ(-10px) transLateX(0px);
  }
  100% {
    transform: translateZ(0px) transLateX(0px);
  }
}
@keyframes movefromright {
  0% {
    transform: translateZ(200px) transLateX(60px);
  }
  80% {
    transform: translateZ(-10px) transLateX(0px);
  }
  100% {
    transform: translateZ(0px) transLateX(0px);
  }
}
@keyframes growup {
  0% {
    transform: scale(0);
  }
  80% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.buttonClass button {
  animation: arrangeButtons 0.7s linear;
}

.buttonClass > button:nth-child(1) {
  animation-delay: 0.6s;
}

.buttonClass > button:nth-child(2) {
  animation-delay: 0.5s;
}
.buttonClass > button:nth-child(3) {
  animation-delay: 0.4s;
}
.buttonClass > button:nth-child(4) {
  animation-delay: 0.3s;
}
.buttonClass > button:nth-child(5) {
  animation-delay: 0.2s;
}
.buttonClass > button:nth-child(6) {
  animation-delay: 0.1s;
}
.buttonClass > button:nth-child(7) {
  animation-delay: 0s;
}
.searchButtons {
  width: 80px;
  height: 30px;
  background-color: #facf0f;
  border-radius: 2px;
  border: none;
  margin-top: 20px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.searchButtons:hover {
  background-color: black;
  color: #facf0f;
  transform: scale(1.2);
}
.mainContainer {
  position: relative;
  margin-top: 2%;
}
.hamMenu {
  height: 30px;
  width: 30px;
  position: absolute;
  left: 3%;
  top: 1vh;
  opacity: 0;
}

@media only screen and (max-width: 768px) {
  .project-button {
    width: 60px;
    margin-right: 20px;
  }
  .about-button {
    width: 80px;
  }
  .container {
    width: 100%;
  }
  .buttonClass {
    column-gap: 10px;
    top: 50px;
    opacity: 0;
  }
  .searchButtons {
    width: 60px;
    height: 20px;
    font-weight: bold;
    font-size: 9px;
    transition: all 0.3s ease-out;
  }
  .searchButtons:hover {
    transform: scale(1.2);
  }
  .projectContainer {
    /*lmargin-top: 4%;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 100px;*/
    width: 80%;
    margin-left: -10%;
  }
  .hamMenu {
    top: 3vh;
    opacity: 1;
  }
  .mainContainer:hover .buttonClass {
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1;
    width: 50vw;
    align-items: center;
    top: 10px;
    height: 100vh;
    left: 0;
    overflow-y: hidden;
    transition: 0.5s ease;
    opacity: 1;
  }
  .mainContainer:hover .hamMenu {
    opacity: 0;
  }

  .mainContainer:hover .searchButtons {
    background-color: transparent;
    color: #facf0f;
    box-shadow: 0 0 0 0 transparent;
    margin: 3vh;
    font-size: 2.8vw;
    font-weight: 700;
    text-align: left;
    text-transform: uppercase;
  }
}
@media only screen and (min-width: 1200px) {
  .projectContainer {
    margin-top: 10px;
    height: 100%;
  }

  .searchButtons {
    width: 100px;
    height: 40px;
    font-size: 16px;
  }
  .header--project > h3 {
    font-size: 30px;
  }
  .header--project > p {
    font-size: 18px;
  }
}

@media only screen and (max-width: 300px) {
  .projectImage {
    width: 260px;
  }
  .imagedisplay {
    height: 390px;
    margin-left: -12vw;
  }
  .buttonClass {
    width: 90%;
  }
}
