.about {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  margin: -30px auto auto auto;
  text-align: center;
}
.about-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin: auto;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: all 0.5s ease-out;
}

.about-button:hover {
  background-color: black;
  color: #facf0f;
  transform: scale(1.2);
}

.about--mainContent {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 80%;
  margin-left: 10px;
  margin-top: 5%;
  column-gap: 5%;

  /*border: 1px solid black;*/
}

.content--two {
  width: 50%;
  height: 100%;
  text-align: left;
  border-radius: 10px;
  padding-left: 20px;
  filter: drop-shadow(5px 5px 2.5px grey);
}
.content--one {
  text-align: left;
  width: 50%;
  height: 100%;
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
}
.content--one > h4 {
  filter: drop-shadow(5px 5px 2.5px grey);
}

.skillsContainer {
  display: flex;
  flex-wrap: wrap;
}

.skills {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 10px;
  animation: down 0.5s linear;
}
.skills > button {
  margin: 5px;
  padding: 5px 20px 5px 20px;
  border-radius: 20px;
  border-color: transparent;
  box-shadow: 5px 5px 5px -2px;
}

.skills > div {
  padding: 20px 20px 5px 20px;
  border-radius: 5px;
  border: 0.5px solid black;
  justify-content: center;
  height: 50px;
  width: 40px;
  display: flex;
  flex-direction: column;
  column-gap: 10px;
  align-items: center;
  box-shadow: 2px 2px 2px -1px;
  transition: all 0.5s ease-out;
}
.skills > div:hover {
  width: 52px;
  height: 56px;

  transition: transform 0.5s ease;
}
.skills > p {
  text-align: center;
  justify-content: center;
}

.skills > div > img {
  filter: drop-shadow(5px 5px 2.5px grey);
}
.tech {
  margin-top: 20px;
}
.tech > button {
  margin: 5px;
  color: white;
  padding: 5px 10px 5px 10px;
  border-radius: 20px;
  border-color: transparent;
  font-size: 10px;
  background-color: rgb(84, 84, 87);
  filter: drop-shadow(5px 5px 2.5px grey);
}

.skills > button:hover {
  background-color: #facf0f;
  border-radius: 20px;
  border-color: transparent;
}

.about--heading {
  margin-top: 20px;
}
.about--heading > p {
  font-size: 20px;
}

.about > p {
  font-size: 16px;
}
.aboutMe {
  width: 100%;
  filter: drop-shadow(5px 5px 2.5px grey);
  animation: movefromleft 0.5s linear;
}
.aboutMe > p {
  font-size: 17px;
}
.underline {
  width: 30px;
  background-color: #facf0f;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #facf0f;
}
@media only screen and (min-width: 1200px) {
  .about--heading > h3 {
    font-size: 30px;
  }

  .content--one > h4 {
    font-size: 20px;
  }
  .content--two > h4 {
    font-size: 20px;
  }
  .content--one > p {
    font-size: 14px;
  }
  .content--two > p {
    font-size: 18px;
  }
  .about > p {
    font-size: 18px;
  }
  .aboutMe {
    width: 100%;
  }

  .about--mainContent {
    column-gap: 5%;
    height: 50%;
    margin-bottom: 10%;
  }
  .skills > div {
    font-size: 18px;
    padding: 30px 30px 10px 30px;
  }
}

@media only screen and (max-width: 768px) {
  body,
  html {
    overflow-y: hidden;
    overflow-x: hidden;
  }

  body::-webkit-scrollbar {
    display: none;
  }

  .about {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    font-size: 16px;
    margin-top: -30px;
  }
  .about--heading {
    font-size: 17px;
    width: fit-content;
    height: fit-content;
  }
  .about--heading > p {
    font-size: 16px;
    width: 90%;
    margin: auto;
  }
  .about--heading > .underline {
    width: 5%;
  }
  .content--one > h4 {
    margin-left: 15%;
  }
  .content--two > h4 {
    margin-left: 15%;
  }
  .aboutMe {
    width: 60%;
    height: 20%;
    font-size: 14px;
    margin-left: 15%;
    margin-top: -10px;
  }
  .aboutMe > p {
    font-size: 12px;
  }
  .about--mainContent {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 60vh;

    cursor: all-scroll;
    margin-top: 0;

    padding-bottom: 10vh;
  }
  .content--one {
    text-align: center;
    width: 100%;
    height: 60%;

    border-radius: 10px;
  }

  .content--two {
    width: 100%;
    height: 40%;

    /* border: 1px solid black;*/
    text-align: center;

    border-radius: 10px;

    margin-top: 10vh;
  }
  .skills {
    width: 60%;

    font-size: 14px;
    margin-left: 15%;
    margin-top: -10px;
  }
  .skills > div {
    padding: 20px 20px 5px 20px;
    border-radius: 5px;
    border: 0.5px solid black;
    justify-content: center;
    height: 1.5em;
    width: 1.5em;
    display: flex;
    flex-direction: column;
    column-gap: 10px;

    align-items: center;
    box-shadow: 2px 2px 2px -1px;
    font-size: 10px;

    transition: width 0.5s, height 0.5s;
  }
}

@media only screen and (max-width: 300px) {
  .aboutMe {
    width: 50%;
    height: 20%;
    font-size: 14px;
    margin-left: 20%;
  }
  .aboutMe > p {
    font-size: 10px;
  }
  .skills {
    width: 50%;

    font-size: 14px;
    margin-left: 20%;
    margin-top: -20px;
  }
  .content--one > h4 {
    margin-left: 20%;
  }
  .content--two > h4 {
    margin-left: 20%;
  }

  .about--mainContent {
    height: 50%;
  }
}
