.imageStyle {
  height: 60px;
  width: 60px;
  background-color: #facf0f;
  border-radius: 50%;
  margin-left: 40px;
  display: flex;
  text-align: center;
  justify-content: center;
}
.socialContainer .imageStyle {
  animation: movefromright 0.7s linear;
}

img {
  height: 80%;
  width: 60%;
  margin: auto;
}
.socialContainer {
  height: 55%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2%;
  animation: down 0.5s linear;
}
.social {
  align-items: center;

  align-items: center;
  width: 100%;

  text-align: left;
  margin-left: auto;
  margin-right: auto;
  height: 40%;
  filter: drop-shadow(5px 5px 2.5px grey);
}
.content {
  display: flex;
  flex-direction: column;

  text-align: center;
  align-items: center;
  height: 70%;

  margin-bottom: 40px;
}

.form {
  display: flex;
  flex-direction: column;
  height: 65%;
  width: 50%;
  gap: 20px;
  margin-left: 10%;
  margin-right: auto;

  margin-top: 0%;
  padding-top: 2%;
  padding-left: 4%;
  padding-bottom: 1%;
  border-bottom: 10%;
  border-radius: 10px;
  background-color: #ffffff;
  border-style: none;
  animation: movefromleft 0.5s linear;
}
.contact {
  display: flex;
  margin-top: 3%;
  height: 80%;
  margin-bottom: 20%;
}

.form > input {
  height: 20%;
  width: 90%;
  border-radius: 5px;
  background-color: #fafafa;
  border-style: none;

  box-shadow: 5px 5px 5px lightgray;
}
.form > textarea {
  width: 90%;
  height: 30%;
  background-color: #fafafa;
  border-style: none;
  box-shadow: 5px 5px 5px lightgray;
}

.contact-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: all 0.5s ease-out;
}
.contact-button:hover {
  background-color: black;
  color: #facf0f;
  transform: scale(1.2);
}
.content > a {
  font-size: 1rem;
}
.content > p {
  font-size: 1rem;
}
.modal {
  margin: auto;
  color: rgb(159, 23, 23);
  font-size: 7px;
  text-align: left;
}
@media only screen and (max-width: 768px) {
  .social {
    display: flex;
    width: 100%;
    height: 20%;
    justify-content: center;
    text-align: left;
  }
  .socialContainer {
    margin-left: 30%;
    margin-right: auto;
    margin-top: 10px;
  }
  .social > div > p {
    font-size: 14px;
    margin-left: 0;
  }
  .social > div > a {
    font-size: 10px;
  }
  .contact-button {
    width: 100px;
  }

  .form {
    width: 80%;
    height: 40%;
    padding-bottom: 5%;
  }

  .contact {
    flex-direction: column;
  }
  .imageStyle {
    height: 30px;
    width: 30px;
  }
  img {
    height: 70%;
    width: 70%;
    margin: auto;
  }
  .content > p {
    margin-bottom: -10px;
  }
}

@media only screen and (max-width: 300px) {
  .social {
    height: 20%;
    width: 100%;
    margin-left: -20%;
  }
}

@media only screen and (min-width: 1200px) {
  .social {
    height: 40%;
    width: 100%;
    column-gap: 20px;
  }
  .social > p {
    font-size: 16px;
  }
  .socialContainer {
    margin-bottom: auto;
  }
}
