.navBar {
  width: 100%;
  height: 62px;
  background-color: #ffff;
  margin-top: 0;
  display: flex;
  justify-content: space-between;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
}
.name {
  margin-left: 40px;
  filter: drop-shadow(5px 5px 2.5px grey);
}
.navItems {
  color: black;
  font-size: 16px;
  margin: auto 40px auto auto;
  text-align: center;
  display: flex;
  gap: 20px;
  font-weight: bold;
}
.link {
  transition: all 0.5s ease-out;
  color: black;
  text-decoration: none;
  padding: 1%;
}

.link:hover {
  background-color: #facf0f;

  transform: scale(1.2);

  filter: drop-shadow(5px 5px 2.5px grey);
}

.home {
  display: flex;
  height: 100%;
  width: 100%;
  overflow-y: hidden;
}
.profile {
  margin-top: 100px;
  height: 700px;
  width: 300px;
}
.profilepic {
  height: 50%;
  width: 100%;
}
.home-button {
  background-color: #facf0f;
  width: 183px;
  height: 44px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  font-weight: bolder;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: all 0.5s ease-out;
}

.home-button:hover {
  background-color: black;
  color: #facf0f;
  transform: scale(1.2);
}
.mainContent {
  display: flex;

  margin: 20vh auto auto;
  text-align: center;
  width: 484px;
  height: 250px;
}
.mainContent > div > button {
  animation: movefromright 0.5s linear;
}
.mainContent > div > p {
  font-size: 15px;
}
.mainContent > div > h1 {
  font-size: 40px;
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  animation: typing;
  animation-duration: 1.5s;
  animation-delay: 0.5s;
  animation-timing-function: steps(30, end);
  animation-fill-mode: forwards;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.socialMedia {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 180px auto auto 0;
  background-color: #facf0f;
  padding: 10px;
  box-shadow: 10px 10px 10px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  animation: up 0.5s linear;
  transition: all 0.5s ease-out;
}
.socialMedia img {
  filter: drop-shadow(5px 5px 2.5px grey);
}
.socialMedia img:hover {
  transform: scale(1.2);
}

@media only screen and (max-width: 768px) {
  .mainContent > div > h1 {
    font-size: 20px;
    width: 100%;
  }
  .mainContent > div > p {
    font-size: 16px;
  }
  .home {
    column-gap: 10px;
    flex-direction: column;
    margin-top: 0;
  }

  .mainContent {
    width: 80%;
    margin-bottom: 90%;
    height: 10%;
    margin: 5vh auto 40vh auto;
  }
  .mainContent > div {
    word-wrap: normal;
  }
  .home {
    position: relative;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  }
  .profile {
    height: 30%;
    width: 80%;
    margin-top: 100px;
  }
  .profilepic {
    width: 80%;
    height: 100%;
    margin-left: 10%;
  }
  .navItems {
    font-size: 10px;
    column-gap: 10px;
    margin: auto 1% auto auto;
  }
  .navBar {
    justify-content: space-between;
  }
  .socialMedia {
    margin-bottom: 100px;
    position: absolute;
    top: 10%;
    left: 0;
  }
  .name {
    margin-left: 10px;
    margin-right: 10px;
    font-size: 16px;
    margin-top: 20px;
  }
}
@media only screen and (min-width: 768px) {
  .mainContent > div > h1 {
    width: 100%;
  }
  .profile {
    margin: 15vh auto auto auto;
  }
  .profilepic {
    height: 100%;
  }
  .socialMedia {
    margin-top: 30vh;
  }

  .mainContent > div > p {
    font-size: 20px;
    width: 100%;
  }
  .mainContent {
    width: 70%;
  }
  .home {
    column-gap: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
  }
}

/* Medium devices (landscape tablets, 768px and up) */

@media only screen and (min-width: 1200px) {
  .home {
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 120vh;
  }
  .profile {
    height: 1300px;
    width: 400px;
  }
  .profilepic {
    height: 80%;
    width: 100%;
  }
  .mainContent {
    width: min-content;
    margin-right: 20%;
  }
  .mainContent > div > h1 {
    font-size: 60px;
  }
  .mainContent > div > p {
    font-size: 20px;
  }
  .name {
    margin-left: 40px;
    font-size: 20px;
  }
  .socialMedia {
    height: 60px;
    width: 40px;
  }
}
@media only screen and (max-width: 300px) {
  .mainContent > div > h1 {
    font-size: 20px;
    width: 100%;
  }
  .mainContent > div > p {
    font-size: 12px;
  }
  .home {
    column-gap: 10px;
    flex-direction: column;
    margin-top: 0;
  }

  .mainContent {
    width: 80%;
    margin-bottom: 90%;
    height: 10%;
    margin: 5vh auto auto auto;
  }
  .mainContent > div {
    word-wrap: normal;
  }
  .mainContent > div > p {
    font-size: 16px;
  }
  .home {
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 110vh;
  }
  .profile {
    height: 30%;
    width: 90%;
    margin-top: 20%;
  }
  .profilepic {
    width: 100%;
    height: 100%;
    margin-left: 10%;
  }
  .navItems {
    font-size: 10px;
    column-gap: 10px;
  }
  .name {
    margin-left: 10px;
    margin-right: 10px;
    font-size: 12px;
    margin-top: 23px;
  }
  .socialMedia {
    top: 0;
  }
}
